import { Settings } from "../config/commonconfig";
import * as microsoftTeams from "@microsoft/teams-js";
//import * as  queryString from 'query-string';

export class SSOSearvice {
    private applicationConfig: any;
    constructor() {
        this.applicationConfig = {
            clientID: "e0f60ff0-327b-43e2-9055-54782bbed141",
            teamsAPI: ["User.Read"],
            campusApI: ["https://campuswebapi.azurewebsites.net/user_impersonation"]
        }
    }


    // 1. Get auth token
    // Ask Teams to get us a token from AAD

    public getAccessTokenClient(): any {
        console.log("getAccessTokenClient start : ", microsoftTeams);
        return new Promise((resolve, reject) => {
            microsoftTeams.initialize();
            microsoftTeams.authentication.getAuthToken({
                successCallback: (result) => {

                    console.log("getAccessTokenClient success : ", result);
                    resolve(result);
                },
                failureCallback: function (error) {
                    console.log("getAccessTokenClient failed : ", error);
                    reject("Error getting token: " + error);
                }
            });

        });

    }

    // 2. Exchange that token for a token with the required permissions
    //    using the web service (see /auth/token handler in app.js)
    public getAccessTokenServer(clientSideToken: string, type: string): Promise<string> {
        return new Promise((resolve, reject) => {
            console.log("getAccessTokenServer start");
            this.getToken(clientSideToken, type).then((response) => {
                console.log("getAccessTokenServer end : ", response);
                resolve(response);
            }).catch(ex => {
                console.log("getAccessTokenServer error : ", ex);
                reject(ex);
            });
        });
    }

    // Show the consent pop-up
    public requestConsent():Promise<string> {
        return new Promise((resolve, reject) => {
            microsoftTeams.authentication.authenticate({
                url: window.location.origin + "/SSO/auth-start",
                width: 600,
                height: 535,
                successCallback: (result) => {
                    if (result) {
                        let jsonData: any = localStorage.getItem(result);
                        localStorage.removeItem(result);
                        let data = JSON.parse(jsonData);
                        resolve(data);
                    }
                },
                failureCallback: (reason) => {
                    reject(JSON.stringify(reason));
                }
            });
        });
    }


    public getToken(token?: string, type?: string): Promise<string> {
        console.log("getToken start");
        let scopes = ["https://campuswebapi.azurewebsites.net/user_impersonation"];
        if (type === "graph")
            scopes = ["https://graph.microsoft.com/User.Read"]
        else if (type === "site")
            scopes = ["https://microsoft.sharepoint.com/AllSites.Read"]
        else if (type === "devops")
            scopes = ["499b84ac-1321-427f-aa17-267ca6975798/user_impersonation"];

        return new Promise((resolve, reject) => {
            const url = "https://cpsui-stg.azurewebsites.net/SSOToken.asmx/GetToken";
            fetch(url, {
                method: "POST",
                body: JSON.stringify({ scopes: scopes.join(" "), clientToken: token }),
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                    charset: "utf-8"
                }
            }).then(result => {
                if (result.status !== 200) {
                    console.log("getToken failed", result);
                    result.json().then(json => {
                        reject({ "error": json.error });
                    });
                } else {
                    result.json().then(json => {
                        resolve(json.d);
                    });
                }
            });
        });
    }

    public async callSessionStorage(tokenName: string, scope: string) {
        let status: boolean = false;
        try {
            var sessionObject: any = sessionStorage.getItem(tokenName);
            if (sessionObject != undefined || sessionObject != null) {
                sessionObject = JSON.parse(sessionObject);
                var expirationDate = new Date(sessionObject.expiresAt);
                if (expirationDate < new Date()) {
                    status = await this.setSessionStorage(tokenName, scope);
                }
            }
            else {
                status = await this.setSessionStorage(tokenName, scope);
            }
        }
        catch (err) {
            return false;
        }
        return status;
    }

    public async setSessionStorage(tokenName: string, scope: string) {
        try {
            const clientSideToken = await this.getAccessTokenClient();
            let serverSideToken: string;
            try {
                serverSideToken = await this.getAccessTokenServer(clientSideToken, scope);
            }
            catch (err) {
                serverSideToken = "error";
            }
            if (serverSideToken == "error") {
                await this.requestConsent().then((response) => {
                    serverSideToken = JSON.parse(response).accessToken;
                })
            }
            var expires = new Date(new Date().getTime() + (60000 * 45));
            let sessionObj = {
                expiresAt: expires,
                token: serverSideToken
            }
            sessionStorage.setItem(tokenName, JSON.stringify(sessionObj));
            return true;
        }
        catch (err) {
            return false;
        }
    }

    public async getScopeToken(type?: string){
        let token = "";
        try{
            type = type ? type : ""
            let key = `SSOFindItToken_${type}`;
            var isSuccess= await this.callSessionStorage(key, type);
            var session = sessionStorage.getItem(key);

            if(session != null && session != undefined){
                token = JSON.parse(session).token;
            }
        }
        catch(error){

        }
        return token;
    }
}
